@import 'variables';
@import 'mixins';

/* ==========================================================================
   #Comments styles - start
   ========================================================================== */

.edgtf-comment-holder {
    @include edgtfRelativeHolderLayout();

    .edgtf-comment-holder-inner {
        margin: 30px 0 0;
    }

    .edgtf-comment-list {
        list-style: none;
        position: relative;
        z-index: 150;
        padding: 0;
        margin: 0;

        li {
            @include edgtfRelativeHolderLayout();
            margin: 30px 0 0;
            padding: 0;
	        
	        &:first-child {
		        margin: 0;
	        }
        }

        .children {
	        margin: 30px 0 0;
            padding: 0 0 0 102px;
        }
    }

    .edgtf-comments-title {
        margin: 0 0 68px;
    }
	
	.edgtf-comment {
		@include edgtfRelativeHolderLayout();
		
		&.edgtf-pingback-comment {
			
			.edgtf-comment-text {
				padding: 0;
				min-height: inherit;
			}
		}
	}

    .edgtf-comment-image {
        position: absolute;
        top: 6px;
	    left: 0;
	    width: 79px;
	    height: 79px;
	    display: block;
	    float: left;

        img {
            display: block;
            border-radius: 4em;
        }
    }

    .edgtf-comment-text {
        padding: 0 0 0 103px;
        min-height: 79px;

        .edgtf-comment-name {
	        @include edgtfRelativeHolderLayout();
            margin: 0;
	        color: $default-heading-color;
	        
	        > * {
		        display: inline-block;
		        vertical-align: top;
	        }
        }
	    
	    .edgtf-comment-name-label {
		    font-weight: 600;
		    letter-spacing: .08em;
		    text-transform: uppercase;
	    }

        .edgtf-comment-date {
	        margin-left: 4px;
	        color: $default-text-color;
	        font-size: 12px;
        }

        .edgtf-text-holder {
            margin: 10px 0 4px;
	        
	        p {
		        margin: 0;
	        }
        }
	
	    .replay,
	    .comment-reply-link,
	    .comment-edit-link {
		    position: relative;
		    margin-right: 11px;
		    font-size: 11px;
		    font-weight: 600;
		    letter-spacing: .2em;
		    text-transform: uppercase;
		    
		    &:after {
			    content: "\f30f";
			    margin-left: 3px;
			    font-family: "Ionicons";
			    speak: none;
			    font-size: 13px;
			    font-style: normal;
			    font-weight: normal;
			    font-variant: normal;
			    text-transform: none;
			    text-rendering: auto;
			    line-height: inherit;
			    -webkit-font-smoothing: antialiased;
			    -moz-osx-font-smoothing: grayscale;
		    }
	    }

        .comment-respond {
            margin: 20px 0;

            .comment-reply-title {
                margin: 0 0 10px;
                font-size: 14px;
            }

            .logged-in-as {
                margin: 0 0 8px;
            }
        }

        #cancel-comment-reply-link {
            margin-left: 10px;
            font-size: 12px;
            color: $first-main-color;

            &:hover {
                color: $default-heading-color;
            }
        }
	
	    ul, ol {
		    
		    li {
			    display: list-item;
		    }
	    }

        li {
            margin: 0 !important;
            padding: 0 !important;
            border: 0 !important;
        }
    }
}

.edgtf-comment-form {
    @include edgtfRelativeHolderLayout();
	margin: 76px 0 70px;
	
	> .comment-respond {
		
		> * {
			position: relative;
			display: inline-block;
			vertical-align: top;
			width: 50%;
			float: left;
			margin: 0;
		}
		
		> form {
			margin-top: 20px;
		}
	}
	
	.edgtf-comment-notes {
		margin: 0 0 35px;
	}
    
    .logged-in-as {
        margin: 0 0 8px;
    }
}

@include ipad-portrait {

    .edgtf-comment-holder {
	    
        .edgtf-comment-list {
	        
            .children {
                padding: 0 0 0 40px;
            }
        }
    }
	
	.edgtf-comment-form {
		
		> .comment-respond {
			
			> * {
				width: 100%;
				float: none;
			}
			
			> form {
				margin-top: 26px;
			}
		}
	}
}

@include phone-landscape {
	
    .edgtf-comment-holder {
	    
        .edgtf-comment-list {
	        
            .children {
                padding: 0;
            }
        }
	
	    .edgtf-comment-image {
		    position: relative;
		    top: 0;
		    float: none;
	    }

        .edgtf-comment-text {
	        padding: 25px 0 0;
	        min-height: inherit;
        }
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

%input-style {
    width: 100%;
    margin: 0 0 20px;
    padding: 14px 18px;
    font-family: inherit;
    font-size: 11px;
    line-height: 20px;
    font-weight: inherit;
    color: $default-text-color;
    background-color: transparent;
    border: 1px solid $default-border-color;
    border-radius: 0;
    outline: 0;
    -webkit-appearance: none;
	cursor: pointer;
    box-sizing: border-box;
    @include edgtfTransition(border-color 0.2s ease-in-out);

    &:focus {
        border-color: $default-text-color;
    }
    
    @include edgtfPlaceholder {
        color: inherit;
    }
}

#respond textarea,
#respond input[type='text'],
input[type='text'],
input[type='email'],
input[type='password'],
.post-password-form input[type='password'] {
    @extend %input-style;
}

.wpcf7-form-control {
	&.wpcf7-text,
	&.wpcf7-number,
	&.wpcf7-date,
	&.wpcf7-textarea,
	&.wpcf7-select,
	&.wpcf7-quiz {
		@extend %input-style;
		
		footer & {
			margin: 0;
		}
	}
}

#submit_comment,
.post-password-form input[type='submit'],
input.wpcf7-form-control.wpcf7-submit {
    @include edgtfButtonDefaultStyle();
    @include edgtfButtonMediumParams();
    @include edgtfButtonSolidColor();
	cursor: pointer;
	
    &:hover {
        @include edgtfButtonSolidHoverColor();
    }
}

p.form-submit,
.wpcf7-form p {
    margin: 0;
}

#respond textarea,
.wpcf7-form textarea {
	display: block;
	padding: 16px 18px;
}

.edgtf-two-columns-form-without-space {

    .wpcf7-form .ajax-loader {
        display: none;
    }
}

.edgtf-cf7-content-style {
	background-color: #fff;
	
	.edgtf-side-menu & {
		background-color: #f3f3f3;
		
		.wpcf7-form-control-wrap {
			
			&:after {
				top: 13px;
				height: 28px;
			}
		}
		
		input.wpcf7-form-control.wpcf7-text {
			height: 54px !important;
			line-height: 54px !important;
		}
		
		input.wpcf7-form-control.wpcf7-submit {
			height: 54px !important;
			line-height: 54px !important;
		}
	}
	
	aside.edgtf-sidebar &,
	.wpb_widgetised_column & {
		background-color: #f3f3f3;
		border: 1px solid #bfbfbf;
	}
	
	.ajax-loader {
		position: absolute;
	}
	
	.wpcf7-form-control-wrap {
		position: relative;
		display: block;
		
		&:after {
			content: '';
			position: absolute;
			top: 16px;
			right: 0;
			width: 1px;
			height: 33px;
			background-color: #cecece;
		}
	}
	
	input.wpcf7-form-control.wpcf7-text {
		height: 65px !important;
		line-height: 65px !important;
		padding: 0 26px !important;
		margin: 0 !important;
		font-size: 14px !important;
		color: #808080 !important;
		background: none !important;
		border: 0 !important;
		
		&:focus {
			color: #000 !important;
		}
	}
	
	input.wpcf7-form-control.wpcf7-submit {
		height: 65px !important;
		line-height: 65px !important;
		padding: 0 34px !important;
		font-size: 13px !important;
		font-weight: 400 !important;
		letter-spacing: 0;
		text-transform: none;
		color: #000 !important;
		background: none !important;
		border: 0 !important;
		
		&:hover {
			color: #ff4d2f !important;
		}
		
		@include laptop-landscape-medium {
			padding: 0 22px !important;
		}
	}
}

.edgtf-cf7-footer-style {
	border: 1px solid #666;
	
	.ajax-loader {
		position: absolute;
	}
	
	.wpcf7-form-control-wrap {
		position: relative;
		display: block;
		
		&:after {
			content: '';
			position: absolute;
			top: 11px;
			right: 0;
			width: 1px;
			height: 25px;
			background-color: #666;
		}
	}
	
	input.wpcf7-form-control.wpcf7-text {
		height: 47px !important;
		line-height: 47px !important;
		padding: 0 25px !important;
		margin: 0 !important;
		font-size: 14px !important;
		color: #666 !important;
		background: none !important;
		border: 0 !important;
		
		&:focus {
			color: #fff !important;
		}
	}
	
	input.wpcf7-form-control.wpcf7-submit {
		height: 47px !important;
		line-height: 47px !important;
		font-size: 13px;
		font-weight: 400;
		letter-spacing: 0;
		text-transform: none;
		padding: 0 30px !important;
		color: #666 !important;
		background: none !important;
		border: 0 !important;
		
		&:hover {
			color: #fff !important;
		}
		
		@include laptop-landscape-medium {
			padding: 0 22px !important;
		}
	}
}

.edgtf-cf7-huge-btn {
	text-align: center;

	input.wpcf7-form-control.wpcf7-submit {
		width: 100%;
	}

	span.ajax-loader {
		display: none;

		&.is-active {
			display: inline-block;
		}
	}
}
/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

/* ==========================================================================
   #Global Owl Slider - begin
   ========================================================================== */
.edgtf-owl-slider {
	visibility: hidden;
	
    .owl-stage-outer {
        z-index: 1;
    }
	
	&.edgtf-slider-is-moving {
		
		.owl-item {
			
			.edgtf-block-drag-link {
				z-index: -1 !important;
			}
		}
	}
    
    &.edgtf-slider-has-both-nav {
        
        .owl-nav {
            
            .owl-prev,
            .owl-next {
                $pos: calc(-50% - 21px); // 21 is is half height of pagination control + top margin
                @include edgtfTransform(translateY($pos));
            }
        }
    }
	
	.owl-nav {
		
		.owl-prev,
		.owl-next {
			position: absolute;
			top: 50%;
			z-index: 3;
			color: $default-heading-color;
			@include edgtfTransform(translateY(-50%));
			
			&:hover {
				
				.edgtf-nav-line {
					width: 33px;
				}
			}
			
			.edgtf-nav-arrow {
				position: absolute;
				top: calc(50% - 7px);
				width: 14px;
				height: 14px;
				border-bottom: 3px solid currentColor;
				border-left: 3px solid currentColor;
			}
			
			.edgtf-nav-line {
				position: absolute;
				top: 50%;
				width: 23px;
				height: 3px;
				background-color: currentColor;
				@include edgtfTransition(width .3s ease-in-out);
			}
		}
		
		.owl-prev {
			left: 30px;
			
			.edgtf-nav-arrow {
				left: 0;
				@include edgtfTransform(rotate(45deg));
			}
			
			.edgtf-nav-line {
				left: 0;
			}
		}
		
		.owl-next {
			right: 30px;
			
			.edgtf-nav-arrow {
				right: 0;
				@include edgtfTransform(rotate(-135deg));
			}
			
			.edgtf-nav-line {
				right: 0;
			}
		}
	}
	
	.owl-dots {
		text-align: center;
		margin: 16px 0 0;
		
		.owl-dot {
			display: inline-block;
			vertical-align: middle;
			
			span {
				display: inline-block;
				vertical-align: middle;
				width: 12px;
				height: 12px;
				margin: 0 6px;
				border-radius: 3em;
				background-color: rgba($default-heading-color, .6);
				box-sizing: border-box;
				-webkit-transition: -webkit-transform .2s ease-in-out, background-color .2s ease-in-out;
				-moz-transition: -moz-transform .2s ease-in-out, background-color .2s ease-in-out;
				transition: transform .2s ease-in-out, background-color .2s ease-in-out;
				@include edgtfTransform(scale(.83));
			}
			
			&:hover,
			&.active {
				
				span {
					background-color: $default-heading-color;
					@include edgtfTransform(scale(1));
				}
			}
		}
	}
	
	.owl-carousel .owl-item img {
		-webkit-transform: translateZ(0); //chrome text cut off fix
	}
}
/* ==========================================================================
   #Global Owl Slider - end
   ========================================================================== */

/* ==========================================================================
   #Global Slick Slider - start
   ========================================================================== */

.edgtf-slick-slider {

	visibility:hidden;

	.slick-slide { 
	    .edgtf-ni-image-holder { 
	        @include edgtfTransition(all .5s ease-in-out .5s);
	        @include edgtfTransform(scale(1.05));
	    }

	    &.slick-current {
	        .edgtf-ni-image-holder {
	            -webkit-transition-delay: 0s !important; //in order to override delays that are set in javascript
	            @include edgtfTransform(scale(1));
	        }
	    }
	}

    .edgtf-slick-arrow {
    	position:absolute;
    	top:50%;
    	color:#121212;
    	z-index:3;
    	cursor:pointer;
    	@include edgtfTransform(translate3d(0,-50%,0));

    	&.edgtf-slick-arrow-prev {
    		left:30px;

    		.edgtf-nav-arrow {
    			@include edgtfTransform(rotate(45deg));
    		}

    		.edgtf-nav-arrow,
    		.edgtf-nav-line {
    			left:0;
    		}
    	}

    	&.edgtf-slick-arrow-next {
    		right:30px;

    		.edgtf-nav-arrow {
    			@include edgtfTransform(rotate(-135deg));
    		}

    		.edgtf-nav-arrow,
    		.edgtf-nav-line {
    			right:0;
    		}
    	}

    	.edgtf-nav-arrow {
    	    position: absolute;
    	    top: calc(50% - 7px);
		    width: 14px;
		    height: 14px;
		    border-bottom: 3px solid currentColor;
		    border-left: 3px solid currentColor;
    	}

    	.edgtf-nav-line {
    		position: absolute;
		    top: 50%;
		    width: 23px;
		    height: 3px;
		    background-color: currentColor;
		    @include edgtfTransition(width .3s ease-in-out);
    	}

    	&:hover {
    		.edgtf-nav-line { 
    			width: 33px;
    		}
    	}
    }
}

/* ==========================================================================
   #Global Slick Slider - end
   ========================================================================== */

/* ==========================================================================
   Access Press Social Count style start
   ========================================================================== */

.apsc-theme-4 .apsc-each-profile a:hover .fa{ 
	@include edgtfTransform(none);
}

.edgtf-accesspress-custom-style .apsc-icons-wrapper {
	position: relative;
	margin: 0 -10px;

	.apsc-each-profile {
		position: relative;
		display: block;
		min-height: initial;
		padding: 0 15px;
		margin: 0 0 20px;
		text-align: left;
		box-sizing: border-box;
		@include edgtfTransition(all .2s ease-in-out);

		a {
			@include edgtfRelativeHolderLayout();
			color: #fff;
			line-height: 50px;
			padding: 0 25px 0 0;
			border-radius: 0;
			box-sizing: border-box;
			@include edgtfTransition(all .2s ease-in-out);

			&.apsc-facebook-icon {
				background-color: #3b5998;
			}

			&.apsc-twitter-icon {
				background-color: #55acee;
			}

			&.apsc-google-plus-icon {
				background-color: #e14e42;
			}

			&.apsc-instagram-icon {
				background-color: #3f729b;
			}

			&.apsc-youtube-icon {
				background-color: #cd201f;
			}

			&.apsc-soundcloud-icon {
				background-color: #ff8800;
			}

			&.apsc-dribble-icon {
				background-color: #c73b6f;
			}

			&.apsc-edit-icon {
				background-color: #333333;
			}

			&.apsc-comment-icon {
				background-color: #2ebd59;
			}

			.apsc-inner-block {

				.social-icon {
					display: table-cell;
					vertical-align: top;

					i {
						position: relative;
						display: inline-block;
						vertical-align: middle;
						width: 52px;
						height: 20px;
						font-size: 14px;
						line-height: 20px;
						margin: 10px 0;
						text-align: center;
					}

					.media-name {
						position: relative;
						display: inline-block;
						vertical-align: middle;
						margin: 0;
						font-size: 12px;
						font-weight: 600;
						text-transform: uppercase;
					}
				}

				.apsc-count,
				.apsc-media-type {
					display: table-cell;
					vertical-align: middle;
					width: 1%;
					text-align: left;
				}

				.apsc-media-type {
					padding: 0 0 0 3px;
				}
			}

			&:hover {
				opacity:0.8;
			}
		}

	}
}

.edgtf-accesspress-custom-style .widget .apsc-icons-wrapper {
	margin: 0;

	.apsc-each-profile {
		width: 100%;
		float: none;
		padding: 0;
		margin: 0 0 14px;

		&:last-child {
			margin: 0;
		}
	}
}

@include laptop-landscape-large {

	.edgtf-accesspress-custom-style .apsc-icons-wrapper {

		.apsc-each-profile {

			a {
				font-size: 12px;
				padding: 0 10px 0 0;

				.apsc-inner-block {

					.social-icon {

						i {
							width: 30px;
							font-size: 14px;
						}

						.media-name {
							font-size: 10px;
							margin: 0 0 0 8px;
						}
					}

					.apsc-count {
						font-size: 10px;
					}

					.apsc-media-type {
						padding: 0 0 0 2px;
					}
				}
			}
		}
	}
}

@include ipad-portrait {

	.edgtf-accesspress-custom-style .apsc-icons-wrapper {
		margin: 0;

		.apsc-each-profile {
			width: 100%;
			float: none;
			padding: 0;
			margin: 0 0 15px;
		}
	}
}
