/* ==========================================================================
   #Back to Top styles - start
   ========================================================================== */
#edgtf-back-to-top {
    position: fixed;
    width: 41px;
    right: 25px;
    bottom: 25px;
    margin: 0;
    z-index: 10000;
    opacity: 0;
    visibility: hidden;

    &.off {
        opacity: 0;
        visibility: hidden;
        @include edgtfTransition(opacity 0.15s ease, visibility .0s .15s);
    }

    &.on {
        opacity: 1;
        visibility: visible;
        @include edgtfTransition(opacity 0.3s ease);
    }

    > span {
        position: relative;
        display: block;
        width: 39px;
        height: 39px;
        line-height: 40px;
        font-size: 15px;
        text-align: center;
        color: #fff;
        background-color: $first-main-color;
        border: 1px solid $first-main-color;
        overflow: hidden;
        border-radius:100%;
        @include edgtfTransition(color 0.2s ease-in-out, background-color 0.2s ease-in-out, border-color 0.2s ease-in-out);

        .edgtf-back-to-top-caret {
        	position: absolute;
        	top:14px;
        	left:15px;
        	width:8px;
        	height:8px;
        	border-top:2px solid #fff;
        	border-right:2px solid #fff;
        	@include edgtfTransform(rotate(-45deg));
        }

        .edgtf-back-to-top-line {
            position: absolute;
            z-index: 1;
            top: 14px;
            left: 19px;
            height: 9px;
            width: 0;
            border: 1px solid #fff;
            @include edgtfTransition(0.25s ease-in);
        }

        &:hover {
            .edgtf-back-to-top-line {
                height: 14px;
                @include edgtfTransition(0.2s ease-in .05s);
            }
        }
    }

    span span {
        display: block;
        line-height: inherit;
    }
}
/* ==========================================================================
   #Back to Top styles - end
   ========================================================================== */

/* ==========================================================================
   #Self Hosted Video styles - begin
   ========================================================================== */
.edgtf-self-hosted-video-holder {
	
    .edgtf-video-wrap {
	
	    .mejs-poster,
        .mejs-container,
        .mejs-overlay-play {
