            border-color: #b1b2b2;
        }

        table tbody th {
            border-right-color: #b1b2b2;
        }

        /*
        *   Specific styles
        */
	    #wp-calendar {
		
		    caption {
			    color: #b1b2b2;
		    }
	    }
	
	    &.widget_rss {
		
		    .edgtf-footer-widget-title {
			
			    .rsswidget {
				    color: #fff;
				
				    &:hover {
					    color: $first-main-color;
				    }
			    }
		    }
	    }
	    
        &.widget_search {

            form {

                > div {
                    border-color: #b1b2b2;
                }
            }

            input {
                color: #b1b2b2;
	            
	            @include edgtfPlaceholder {
		            color: inherit;
	            }

                &[type='submit'] {
                    color: #b1b2b2;
                }
            }
        }

        &.widget_archive,
        &.widget_categories,
        &.widget_text {

            select {
                color: #b1b2b2;
                border-color: #b1b2b2;

                option {
                    background-color: #b1b2b2;
                }
            }

        }

        &.widget_tag_cloud {

            a {
                color: #b1b2b2;

                &:after {
                    color: inherit;
                }

                &:hover {
                    color: $first-main-color;
                }
            }
        }

		&.widget_nav_menu {

			ul {
				clear: both;

				li {
					padding: 5px 0;

					a {
						font-size: 13px;
						line-height: 17px;
						letter-spacing: 0.1em;
						text-transform: uppercase;
					}
				}
			}
		}

		&.widget.widget_edgtf_twitter_widget {

			.edgtf-twitter-widget {

				&.edgtf-twitter-standard {

					li {

						.edgtf-twitter-icon {
							color: #fff;
						}
					}
				}
			}
		}

		&.widget_recent_comments {

			.recentcomments {
				margin-bottom: 10px;
				
				&:last-child {
					margin-bottom: 0;
				}
				
				&:before {
					display: inline-block;
					content: "\e065";
					font-family: 'ElegantIcons';
					speak: none;
					font-style: normal;
					font-weight: normal;
					font-variant: normal;
					text-transform: none;
					margin: 0 13px 0 0;
				}

				.comment-author-link {
					vertical-align: 1px;
					font-size: 12px;
					text-decoration: underline;
				}
				
				> a {
					display:  block !important; // important to override inline css generated by the widget
					padding: 0 0 0 27px !important; // important to override inline css generated by the widget
				}
			}
		}
    }
}

.edgtf-top-bar {

	a {
		color: rgba(#fafafa, 0.6);

		&:hover {
			color: $first-main-color;
		}
	}

    .widget {
        display: inline-block;
        vertical-align: middle;
        margin: 0;

        p {
            margin: 0;
        }

        &.widget_text {
            margin: 0 8px 0 0;
        }

        &.widget_nav_menu {

            ul {
                clear: both;

                li {
                    float: left;
                    padding: 0 8px;
                    margin: 0;
                    line-height: 14px;
                    border-left: 1px solid #fff;
                }
            }
        }

		&.edgtf-news-latest-news-widget {

			.edgtf-news-lnw-title {
				margin: 0 11px 0 0;
				color: #fafafa;
			}
		}

		&.edgtf-news-weather-widget {
			color: rgba(#fafafa, 0.6);
			font-size: 12px;

			.edgtf-weather-temperature {
				margin: 0 0 0 21px;
				font-size: 18px;
			}
		}
    }
}

.edgtf-page-header {

	.widget {

		&.widget_nav_menu {

			ul {
				clear: both;

				li {
					float: left;
					padding: 0 15px;
					margin: 0;

					a {
						position: relative;
						font-size: 12px;
						font-weight: 600;
						letter-spacing: 0.1em;
						text-transform: uppercase;

						&:hover {
							color: $default-heading-color;

							&:after {
								bottom: -2px;
								width: 6px;
								height: 6px;
								opacity: 1;
							}
						}

						&:after {
							position: absolute;
							display: block;
							content: " ";
							left: 50%;
							bottom: -4px;
							width: 4px;
							height: 4px;
							border-radius: 50%;
							background-color: $first-main-color;
							opacity: 0;
							@include edgtfTransition(width 0.1s ease-in, height 0.1s ease-in, bottom 0.1s ease-in);
							@include edgtfTransform(translateX(-50%) translateY(100%));
						}
					}
				}
			}
		}
	}
}

.edgtf-logo-area {

	.widget {
		
		&.widget_nav_menu {
			margin: 14px 0 0;
			
			ul {

				&.sub-menu {
					display: none;
				}

				li {
					
					&:last-child {
