/* Slider */

.slick-slider {
    position: relative;
    display: block;
    box-sizing: border-box;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    -ms-touch-action: pan-y;
    touch-action: pan-y;
    -webkit-tap-highlight-color: transparent;
    transition: all attr(data-slider-speed-animation) ease;
}
.slick-list {
    position: relative;
    overflow: hidden;
    display: block;
    margin: 0;
    padding: 0;

    &:focus {
        outline: none;
    }

    &.dragging {
        cursor: pointer;
        cursor: hand;
    }
}
.slick-slider .slick-track,
.slick-slider .slick-list {
    -webkit-transform: translate3d(0, 0, 0);
    -moz-transform: translate3d(0, 0, 0);
    -ms-transform: translate3d(0, 0, 0);
    -o-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
}

.slick-track {
    position: relative;
    left: 0;
    top: 0;
    display: block;

    &:before,
    &:after {
        content: "";
        display: table;
    }

    &:after {
        clear: both;
    }

    .slick-loading & {
        visibility: hidden;
    }
}
.slick-slide {
    float: left;
    height: 100%;
    min-height: 1px;
    [dir="rtl"] & {
        float: right;
    }
    img {
        display: block;
    }
    &.slick-loading img {
        display: none;
    }

    display: none;

    &.dragging img {
        pointer-events: none;
    }

    .slick-initialized & {
        display: block;
    }

    .slick-loading & {
        visibility: hidden;
    }

    .slick-vertical & {
        display: block;
        height: auto;
        border: 1px solid transparent;
    }
}

.slick-dots {
    position: absolute;
    left: 0;
    bottom: 20px;
    width: 100%;
    text-align:center;
    margin: 0;
    z-index: 1;

    li {
        display: inline-block;
        vertical-align: middle;

        button {
            position:relative;
            font-size: 0;
            line-height: 0;
            display: block;
            width: 20px;
            height: 20px;
            padding: 5px;
            cursor: pointer;
            color: transparent;
            border: 0;
            outline: none;
            background: transparent;

            &:before {
                content: "";
                font-size: 6px;
                line-height: 20px;
                position: absolute;
                top: 0px;
                left: 0px;
                width: 12px;
                height: 12px;
                text-align: center;
                opacity: .6;
                background-color: #fff;
                border-radius:100%;
                @include edgtfTransform(scale(.83));
                @include edgtfTransition(all .2s ease-in-out);
            }
        }

        &.slick-active {
            button { 
                &:before { 
                    opacity:1;
                    @include edgtfTransform(scale(1));
                }
            }
        }
    }
}

.slick-arrow.slick-hidden {
    display: none;
}
