/* ==========================================================================
   Contact Form widget style - begin
   ========================================================================== */

.edgtf-contact-form-7-widget {
	margin: 0;
	
	.edgtf-widget-title {
		margin: 0 0 37px;
	}
	
	&.edgtf-widget-boxed-layout {
		padding: 22px 29px 38px;
		background: #fff;
		
		.ajax-loader {
			position: absolute;
		}
	}

	.edgtf-contact-form-image {
		display: block;
		width: auto;
		margin: 0 auto;
	}

	.edgtf-cf7-heading {
		margin: 20px 30px 34px;
		text-align: center;
		
		@include laptop-landscape-medium {
			margin: 20px 12px 34px;
		}
	}

	.edgtf-cf7-text {
		display: block;
		margin: 6px 0 0;
		color: #ccc;
		text-align: center;
		font-size: 13px;
	}
	
	input.wpcf7-form-control {
	
		&.wpcf7-submit {
			width: 100%;
		}
	}
}
/* ==========================================================================
   Contact Form widget style - end
   ========================================================================== */